<template>
    <transition name="move" >
        <div class="food" v-show="showFlag" ref="food">
            <div class="food-content">
                <div class="img-header">
                    <img :src="food.image">
                    <div class="back" @click="hide">
                        <i class="icon-arrow_lift"></i>
                    </div>
                </div>
                <div class="content">
                    <h1 class="title">{{food.name}}</h1>
                    <div class="detail">
                        <span class="sell-count">月售{{food.sellCount}}份</span>
                        <span class="rating">{{food.rating}}%</span>
                    </div>
                    <div class="price">
                        <span class="now">¥&nbsp;{{food.price}}</span>
                        <span class="old" v-if="food.oldPrice">¥&nbsp;{{food.oldPrice}}</span>
                    </div>
                    <div class="cartcontrol-wrapper">
                        <cartcontrol :food="food" @addBall="addBall"></cartcontrol>
                    </div>
                    <transition name="fade">
                        <div class="buy" @click.stop="addFirst($event)" v-show="!food.count || food.count === 0">加入购物车</div>
                    </transition>
                </div>
                <split v-show="food.info"></split>
                <div class="info" v-show="food.info">
                    <h1 class="title">商品信息</h1>
                    <p class="text">{{food.info}}</p>
                </div>
                <split></split>
                <div class="rating">
                    <h1 class="title">商品评价</h1>
                    <ratingselect :selectType="selectType" :onlyContent="onlyContent" :desc="desc" :ratings="food.ratings"
                                        @select="selectRating" @toggle="toggleContent"></ratingselect>
                    <div class="rating-wrapper">
                        <ul v-show="food.ratings && food.ratings.length">
                            <li v-for="rating in food.ratings" class="rating-item border-1px" v-show="needShow(rating.rateType,rating.text)">
                                <div class="user">
                                    <span class="name">{{rating.username}}</span>
                                    <img class="avatar" :src="rating.avatar" width="12" height="12">
                                </div>
                                <div class="time">{{rating.rateTime | formatDate}}</div>
                                <p class="text">
                                    <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>{{rating.text}}
                                </p>
                            </li>
                        </ul>
                        <div class="no-rating" v-show="!food.ratings || !food.ratings.length"></div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
import cartcontrol from 'components/cartcontrol/cartcontrol';
import split from 'components/split/split';
import ratingselect from 'components/ratingselect/ratingselect';
import Vue from 'vue';
import BScroll from 'better-scroll';

const ALL = 2;

export default {
    props: {
        food: {
            type: Object
        }
    },
    data() {
        return {
            showFlag: false,
            selectType: ALL, // 选择的评价类型
            onlyContent: false,// 是否只显示有内容的评价
            desc:{ // 评价的选择描述
                all: '全部',
                positive: '推荐',
                negative: '吐槽',
            }
        }
    },
    methods: {
        show() {
            this.showFlag = true;
            this.$nextTick(() => {
                if(!this.scroll){
                    this.scroll = new BScroll(this.$refs.food,{
                        click: true
                    })
                }else{
                    this.scroll.refresh();
                }
            })
        },
        hide() {
            this.showFlag = false;
        },
        addFirst(event) {
            if(!event._constructed){
                return;
            }
            this.$emit('addBall',event.target);
            Vue.set(this.food,'count',1);
        },
        addBall(target){
            this.$emit('addBall',target);
        },
        needShow(type,text) {
            if(this.onlyContent && !text){
                return false;
            }
            if(this.selectType === ALL){
                return true;
            }else{
                return this.selectType === type;
            }
        },
        selectRating(type) { //父组件接受ratingselect组件派发 事件
            this.selectType = type;
            this.$nextTick(() => { // 在dom更新之后再refresh
                this.scroll.refresh()
            });
        },
        toggleContent() {
          this.onlyContent = !this.onlyContent;
          this.$nextTick(() => {
                this.scroll.refresh()
            });
        }
    },
    components: {
        cartcontrol,
        split,
        ratingselect
    }
}
</script>

<style lang="scss">
@import "../../common/scss/mixin";

.food{
    position: fixed;
    left: 0;
    top: 0;
    bottom: 48px;
    z-index: 30;
    width: 100%;
    background: #fff;
    transform: translate3d(0,0,0);
    &.move-enter,&.move-leave-active{
        transform: translate3d(100%,0,0);
    }
    &.move-enter-active,&.move-leave-active{
        transition: all 0.3s;
    }
    .img-header{
        position: relative;
        width: 100%;
        height: 0;
        padding-bottom: 100%;
        img{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
        .back{
            position: absolute;
            left: 0;
            top: 10px;
            .icon-arrow_lift{
                display: block;
                padding: 10px;
                font-size: 20px;
                color: #fff
            }
        }
    }
    .content{
        position: relative;
        padding: 18px;
        .title{
            line-height: 14px;
            margin-bottom: 8px;
            font-size: 14px;
            font-weight: 700;
            color: (7,17,27);
        }
        .detail{
            margin-bottom: 18px;
            line-height: 10px;
            height: 10px;
            font-size: 0;
            .sell-count,.rating{
                font-size: 10px;
                color: rgb(147, 153, 159)
            }
            .sell-count{
                margin-right: 12px;
            }
        }
        .price{
            font-weight: 700;
            line-height: 24px;
            .now{
                margin-right: 8px;
                font-size: 14px;
                color: rgb(240, 20, 20);
            }
            .old{
                text-decoration: line-through;
                font-size: 10px;
                color: rgb(147,153,159);
            }
        }
        .cartcontrol-wrapper{
            position: absolute;
            right: 12px;
            bottom: 12px;
        }
        .buy{
            position: absolute;
            right: 18px;
            bottom: 18px;
            z-index: 10;
            height: 24px;
            line-height: 24px;
            padding: 0 12px;
            box-sizing: border-box;
            border-radius: 12px;
            font-size: 10px;
            color:#fff;
            background: rgb(0,160,220);
            opacity: 1;
            &.fade-enter,&.fade-leave-active{
                opacity: 0;
            }
            &.fade-enter-active,&.fade-leave-active{
                transition: all 0.2s
            }
        }
    }
    .info{
        padding: 18px;
        .title{
            line-height: 14px;
            margin-bottom: 6px;
            font-size: 14px;
            color: rgb(7, 17, 27);
        }
        .text{
            line-height: 24px;
            font-size: 12px;
            padding: 0 8px;
            color: rgb(77, 85, 93);
        }
    }
    .rating{
        padding-top: 18px;
        .title{
            line-height: 14px;
            margin-left: 18px;
            font-size: 14px;
            color: rgb(7, 17, 27);
        }
        .rating-wrapper{
            padding: 0 18px;
            .rating-item{
                position: relative;
                padding: 16px 0;
                @include border-1px(rgba(7,17,27,0.1));
                .user{
                    position: absolute;
                    right: 0;
                    top: 16px;
                    line-height: 12px;
                    font-size: 0;
                    .name{
                        display: inline-block;
                        margin-right: 6px;
                        vertical-align: top;
                        font-size: 10px;
                        color: rgb(147, 153, 159);
                    }
                    .avatar{
                        border-right: 50%;
                    }
                }
                .time{
                    margin-bottom: 6px;
                    line-height: 12px;
                    font-size: 10px;
                    color: rgb(147, 153, 159);
                }
                .text{
                    line-height: 16px;
                    font-size: 12px;
                    color: rgb(7, 17, 27);
                    .icon-thumb_up, .icon-thumb_down{
                        margin-right: 4px;
                        line-height: 16px;
                        font-size: 12px;
                    }
                    .icon-thumb_up{
                        color: rgb(0, 160, 220);
                    }
                    .icon-thumb_down{
                        color: rgb(147, 153, 159);
                    }
                }
            }
        }
    }
}
</style>



