<template>
    <div class="ratings" ref="ratings">
        <div class="ratings-content">
            <div class="overview">
                <div class="overview-left">
                    <h1 class="score">{{seller.score}}</h1>
                    <div class="title">综合评分</div>
                    <div class="rank">高于周边商家{{seller.rankRate}}%</div>
                </div>
                <div class="overview-right">
                    <div class="score-wrapper">
                        <span class="title">服务态度</span>
                        <star :size="36" :score="seller.serviceScore"></star>
                        <span class="score">{{seller.serviceScore}}</span>
                    </div>
                    <div class="score-wrapper">
                        <span class="title">商品评分</span>
                        <star :size="36" :score="seller.foodScore"></star>
                        <span class="score">{{seller.foodScore}}</span>
                    </div>
                    <div class="delivery-wrapper">
                        <span class="title">送达时间</span>
                        <span class="delivery">{{seller.deliveryTime}}分钟</span>
                    </div>
                </div>
            </div>
            <split></split>
            <ratingselect :selectType="selectType" :onlyContent="onlyContent" :ratings="ratings"
                                        @select="selectRating" @toggle="toggleContent"></ratingselect>
            <div class="rating-wrapper">
                <ul>
                    <li class="rating-item" v-for="rating in ratings" v-show="needShow(rating.rateType,rating.text)">
                        <div class="avatar">
                            <img :src="rating.avatar" width="28" height="28">
                        </div>
                        <div class="content">
                            <h1 class="name">{{rating.username}}</h1>
                            <div class="star-wrapper">
                                <star :size="24" :score="rating.score"></star>
                                <span class="delivery">{{rating.deliveryTime}}</span>
                            </div>
                            <p class="text">{{rating.text}}</p>
                            <div class="recommend" v-show="rating.recommend && rating.recommend.length">
                                <span class="icon-thumb_up"></span>
                                <span class="item" v-for="item in rating.recommend">{{item}}</span>
                            </div>
                            <div class="time">{{rating.rateTime | formatDate}}</div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import star from 'components/star/star';
import split from 'components/split/split';
import ratingselect from 'components/ratingselect/ratingselect';
import {ratingsData} from '../../service/getData';
import BScroll from 'better-scroll';


const ERR_OK = 0;
const ALL = 2;
export default {
    data() {
        return {
            selectType: ALL, // 选择的评价类型
            onlyContent: false,// 是否只显示有内容的评价
            ratings: [],
        }
    },
    props: {
        seller: {
            type: Object
        }
    },
    created() {
        ratingsData().then((res) => {
            if(res.errorno == ERR_OK){
                this.ratings = res.data;
                this.$nextTick(() => {
                    this.scroll = new BScroll(this.$refs.ratings,{
                        click: true
                    })
                })
            }
        })
    },
    methods: {
        selectRating(type) { //父组件接受ratingselect组件派发 事件
            this.selectType = type;
            this.$nextTick(() => { // 在dom更新之后再refresh
                this.scroll.refresh()
            });
        },
        toggleContent() {
            this.onlyContent = !this.onlyContent;
            this.$nextTick(() => {
                this.scroll.refresh()
            });
        },
        needShow(type,text) {
            if(this.onlyContent && !text){
                return false;
            }
            if(this.selectType === ALL){
                return true;
            }else{
                return this.selectType === type;
            }
        }
    },
    components: {
        star,
        split,
        ratingselect
    }

}
</script>

<style lang="scss">
.ratings{
    position: absolute;
    top: 174px;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    .overview{
        display: flex;
        padding: 18px 0;
        .overview-left{
            flex: 0 0 137px;
            padding: 6px 0;
            width: 137px;
            border-right: 1px solid rgba(7,17,27,0.1);
            text-align: center;
            @media only screen and (max-width: 320px){
                flex: 0 0 120px;
                width: 120px;
            };
            .score{
                font-size: 24px;
                line-height: 28px;
                color: rgb(255, 153, 0);
                margin-bottom: 6px;
            }
            .title{
                margin-bottom: 8px;
                line-height: 12px;
                font-size: 12px;
                color: rgb(7,17,27);
            }
            .rank{
                font-size: 10px;
                line-height: 10px;
                color: rgb(147, 153, 159);
            }
        }
        .overview-right{
            flex: 1;
            padding: 6px 0 6px 24px;
            @media only screen and (max-width: 320px){
                padding-left: 6px;
            };
            .score-wrapper{
                margin-bottom: 8px;
                font-size: 0;
                .title{
                    display: inline-block;
                    vertical-align: top;
                    font-size: 12px;
                    line-height: 18px;
                    color: rgb(7, 17, 27);
                }
                .star{
                    display: inline-block;
                    vertical-align: top;
                    margin: 0 12px;
                }
                .score{
                    display: inline-block;
                    vertical-align: top;
                    font-size: 12px;
                    line-height: 18px;
                    color: rgb(255, 153, 0)
                }
            }
            .delivery-wrapper{
                font-size: 0;
                .title{
                    font-size: 12px;
                    line-height: 18px;
                    color: rgb(7, 17, 27);
                }
                .delivery{
                    font-size: 12px;
                    margin-left: 12px;
                    color: rgb(147, 153, 159);
                }
            }
        }
    }
    .rating-wrapper{
        padding: 0 18px;
        .rating-item{
            display: flex;
            padding: 18px 0;
            border-bottom: 1px solid rgba(7, 17, 27, 0.1);
            .avatar{
                flex:0 0 28px;
                width: 28px;
                margin-right: 12px;
                img{
                    border-right: 50%;
                }
            }
            .content{
                position: relative;
                flex: 1;
                .name{
                    margin-bottom: 4px;
                    line-height: 12px;
                    font-size: 10px;
                    color: rgb(7,17,27);
                }
                .star-wrapper{
                    margin-bottom: 6px;
                    font-size: 0;
                    .star{
                        display: inline-block;
                        vertical-align: top;
                        margin-right: 6px;
                    }
                    .delivery{
                        display: inline-block;
                        vertical-align: top;
                        font-size: 10px;
                        line-height: 12px;
                        color: rgb(147, 153, 159);
                    }
                }
                .text{
                    margin-bottom: 8px;
                    font-size: 12px;
                    line-height: 18px;
                    color: rgb(7, 17, 27);
                }
                .recommend{
                    font-size: 0;
                    line-height: 16px;
                    .icon-thumb_up,.item{
                        display: inline-block;
                        margin: 0 8px 4px 0;
                        font-size: 9px;
                    }
                    .icon-thumb_up{
                        color: rgb(0, 160, 220);
                    }
                    .item{
                        padding: 0 6px;
                        border: 1px solid rgba(7, 17, 27, 0.1);
                        border-radius: 1px;
                        color: rgb(147, 153, 159);
                        background: #fff;
                    }
                }
                .time{
                    position: absolute;
                    top: 0;
                    right: 0;
                    line-height: 12px;
                    font-size: 10px;
                    color: rgb(147, 153, 159);
                }
            }
        }
    }
}
</style>



